.planet_data {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0;
    margin: 0 0 0 80px;
    border-radius: 20px !important;
    transition: all 0.2s;

    &:hover{
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
    }
}