@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
*{font-family:'Roboto', sans-serif}* ::-webkit-scrollbar{width:10px}* ::-webkit-scrollbar-track{background:transparent}* ::-webkit-scrollbar-thumb{border-radius:5px;background:#09D3AC}* ::-webkit-scrollbar-thumb:hover{cursor:pointer;background:#09AB8C}body{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.container{display:flex;flex-direction:column;align-items:center}.container .upper_section{width:100%;height:auto;position:relative;display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.container .bottom_section{margin:20px 0 20px 0;width:100%;display:flex;flex-direction:row;align-items:flex-start;justify-content:space-between;height:auto}.container .bottom_section ::-webkit-scrollbar{width:5px}.container .bottom_section .left_part{display:flex;flex-direction:row;align-items:center;justify-content:center;padding-right:10px;width:30%;min-height:300px;height:450px}.container .bottom_section .left_part ::-webkit-scrollbar-track{margin-top:20px;margin-bottom:20px;background:transparent}.container .bottom_section .right_part{display:flex;flex-direction:column;align-items:center;justify-content:space-between;padding-left:10px;width:70%;height:450px}.bottom_moved{margin-top:0 !important}.moved{height:440px !important}

header{margin-bottom:20px;width:100%;height:80px;box-shadow:0 0 20px rgba(0,0,0,0.4)}header .container{height:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-between}header .container .logo{font-size:30px}header .container .buttons button{outline:none;background:none;border:none;color:#F3F3F3;font-size:20px;padding:0;margin:0 30px;-webkit-transition:all 0.2s;transition:all 0.2s}header .container .buttons button:hover{color:#09D3AC}header .container .buttons button:active{opacity:0.05}header .container .buttons button:first-child{margin-left:0}header .container .buttons button:last-child{margin-right:0}

.toggle_random_planet{outline:none !important;position:absolute;top:0;left:0;padding:0;border:none;border-radius:20px;width:60px;height:100%;background-color:#303030;-webkit-transition:all 0.2s;transition:all 0.2s}.toggle_random_planet:hover{box-shadow:0 0 20px rgba(0,0,0,0.4)}.toggle_random_planet:active{opacity:0.5}.toggle_random_planet span{height:100%;word-wrap:unset;-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;-webkit-transform:rotate(180deg);transform:rotate(180deg);color:#09d3ac;font-size:18px;-webkit-transition:all 0.2s;transition:all 0.2s;padding-left:16.5px;padding-right:16.5px}.toggle_random_planet span:hover{padding-top:10px}.toggle_random_planet span:active{padding-top:30px}.in_list{top:0;height:60px !important;width:321px}.in_list span{display:inherit;width:100%;padding:16.5px 0;-webkit-writing-mode:horizontal-tb;-ms-writing-mode:lr-tb;writing-mode:horizontal-tb;-webkit-transform:rotate(0deg);transform:rotate(0deg)}.in_list span:hover{padding-top:16.5px;padding-left:10px}.in_list span:active{padding-left:30px}

.planet_data{display:flex;flex-direction:row;justify-content:center;align-items:center;width:100%;padding:0;margin:0 0 0 80px;border-radius:20px !important;-webkit-transition:all 0.2s;transition:all 0.2s}.planet_data:hover{box-shadow:0 0 20px rgba(0,0,0,0.4)}

@-webkit-keyframes spinner{0%{top:92px;left:92px;width:0;height:0;opacity:1}100%{top:16px;left:16px;width:152px;height:152px;opacity:0}}@keyframes spinner{0%{top:92px;left:92px;width:0;height:0;opacity:1}100%{top:16px;left:16px;width:152px;height:152px;opacity:0}}.spinner div{position:absolute;border-width:8px;border-style:solid;opacity:1;border-radius:50%;-webkit-animation:spinner 1.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;animation:spinner 1.4s cubic-bezier(0, 0.2, 0.8, 1) infinite}.spinner div:nth-child(1){border-color:#09d3ac}.spinner div:nth-child(2){border-color:#ffffff;-webkit-animation-delay:-0.7s;animation-delay:-0.7s}.loading_spinner{width:200px;height:200px;display:inline-block;overflow:hidden;background:transparent}.spinner{width:100%;height:100%;position:relative;-webkit-transform:translateZ(0) scale(1);transform:translateZ(0) scale(1);-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-transform-origin:0 0;transform-origin:0 0}.spinner div{box-sizing:content-box}

.planet_content{width:100%;height:100%;display:flex;flex-direction:row;align-items:center !important;justify-content:space-between}.planet_content .planet_image{margin:35px 40px;width:200px;height:200px;border-radius:20px;-webkit-transition:all 0.2s;transition:all 0.2s}.planet_content .planet_image:hover{-webkit-transform:scale(1.1);transform:scale(1.1);box-shadow:0 0 20px rgba(0,0,0,0.4)}.planet_content .planet_description{cursor:default;display:flex;flex-direction:row;align-items:center}.planet_content .planet_description h2{margin:0 50px}.planet_content .planet_description ul{margin:35px 40px}.planet_content .planet_description ul li{font-size:18px}.planet_content .term{color:#09D3AC}

.error_message{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:30px;color:#09d3ac}.error_message span{font-size:20px}.error_message img{width:100px;height:100px}

.item_list{width:100%;height:100%;-webkit-transition:all 0.2s;transition:all 0.2s;border-radius:20px;margin-top:0;padding:0;margin-left:0;margin-right:0;overflow-y:scroll}.item_list li{border-left:none;border-right:none;display:flex;align-items:center;width:100%;height:45px;font-size:16px;min-width:250px;max-width:400px;cursor:pointer;padding:10.5px 0 10.5px 0;-webkit-transition:all 0.2s;transition:all 0.2s}.item_list li:hover{background:#2E2E2E;box-shadow:0 0 20px rgba(0,0,0,0.4);color:#09D3AC}.item_list li:first-child{border-top:none;border-top-right-radius:20px;border-top-left-radius:20px}.item_list li:last-child{border-bottom:none;border-bottom-right-radius:20px;border-bottom-left-radius:20px}.item_list li span{width:100%;padding:10.5px 20px 10.5px 30px;-webkit-transition:all 0.2s;transition:all 0.2s}.item_list li span:hover{padding-left:40px}.item_list li span:active{opacity:0.05}.list_moved{height:360px;margin-top:80px}

.details{padding:35px 40px;margin-bottom:0;width:100%;min-height:300px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;border-radius:20px !important;-webkit-transition:all 0.2s;transition:all 0.2s}.details:hover{box-shadow:0 0 20px rgba(0,0,0,0.4)}.details .message{width:100%;display:flex;justify-content:center;align-items:center}.details .message h2{padding:0 20px;text-align:center;margin:0}.details .content{width:100%}.details .content .error_message{width:100%}.details .content .error_message img:hover{box-shadow:none;-webkit-transform:scale(1);transform:scale(1)}.details .planet{width:200px;height:200px}.details .starship{width:300px;height:200px}.details .person{width:200px;height:275px}.details img{border-radius:20px;-webkit-transition:all 0.2s;transition:all 0.2s;object-fit:scale-down}.details img:hover{z-index:10;-webkit-transform:scale(1.1);transform:scale(1.1);box-shadow:0 0 20px rgba(0,0,0,0.4)}

.description{z-index:0;height:100%;width:50%;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.description h2{margin:0 15px 30px 15px;color:#09D3AC}.description .list-group-flush{min-width:250px;width:auto;max-height:100%;height:auto;overflow-y:scroll;overflow-x:hidden}.description .list-group-flush li{width:100%}.description .list-group-flush li .term{color:#09D3AC}

